body {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }

.header {
  width: 100%;
  display: flex;
  justify-content: center; }
  .header .header__logo {
    width: 70%;
    height: 60%; }

.giphy {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: center; }

.search {
  width: 80%;
  height: 30%;
  display: flex;
  justify-content: center; }
  .search .search__field {
    width: 80%;
    height: 90%;
    border: 2px solid #814cec;
    display: flex; }
  .search .search__button {
    width: 7%;
    height: 100%;
    border: none;
    background-image: url("images/search.gif");
    background-position: center;
    background-size: cover; }
    .search .search__button img {
      width: 100%; }
